.subheader {
  height: 150px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #fff6a4;
}

.subheader p {
  color: #737383;
  font-size: 18px;
  line-height: 28px;
  font-style: italic;
  text-align: center;
  max-width: 600px;
}

.subheader p a {
  color: #41414a;
  text-decoration: none;
  -webkit-transition: color .3s ease-in-out;
  -o-transition: color .3s ease-in-out;
  transition: color .3s ease-in-out;
}

.subheader p a:hover {
  color: #de9400;
}



